import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import StyledMenu from './DDmenu';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';

import { MenuBarProps } from '../App';
export default function MenuAppBar({props}: {props: MenuBarProps} ) {
  const [auth, setAuth] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);



  // Related to the user picked List of Products
  
  const removeMe = (index: number) => props.setCurrentProducts(props.currentProducts.filter((_, i) => i !== index));

  function ButtonList( {currentProducts}: {currentProducts: string[]} ) {
    return (
      <Stack sx={{marginLeft: 'auto', marginRight: '0'}}direction="row" spacing={'.5vw'}>        
        {props.currentProducts.map((value, index) => (
          <Button onClick={() => removeMe(index)} sx={{overflow: 'hidden', textWrap: 'nowrap', width: '7vw'}} variant="contained" startIcon={<CloseIcon />}>
            <div style={{width: '6vw', overflow: 'hidden'}} className="productText">
              {value}
            </div>
        </Button>
        ))}
      </Stack>
    );
  }



  return (
    <Box sx={{ flexGrow: 1}}>
      <AppBar sx={{backgroundColor: 'rgb(52, 140, 235)'}}position="static">
        <Toolbar>
        <StyledMenu props = {props}/>    
          <ButtonList currentProducts = {props.currentProducts}/>
          
        </Toolbar>
      </AppBar>
    </Box>
  );
}