import React from 'react';
import MenuAppBar from './Components/AppBar'
import './App.css';
 
export interface MenuBarProps {
  currentProducts: string[]
  setCurrentProducts: React.Dispatch<React.SetStateAction<string[]>>
}


function App() {
  const [currentProducts, setCurrentProducts] = React.useState<string[]>([
    "cheetos big",
    "doritoes",
    "yuka fries"
  ])

  const menuBarProps: MenuBarProps = {
    currentProducts: currentProducts,
    setCurrentProducts: setCurrentProducts
  };


  return (
    <div className="App">
      <MenuAppBar props = {menuBarProps}/>
      <header className="App-header">
      </header>
    </div>
  );
}

export default App;
